import { useSelector } from 'react-redux';
import TodoItem from './TodoItem';

const TodoList = () => {
  // useSelector -   redux,          - store
  //  -,  ,    ,       (todos)     
  //   (todos -   initialState)   
    const todos = useSelector(state => state.todos.todos);

  return (
    <ul className="comments-container">
      {todos.map((todo) => (
        <TodoItem
          key={todo.id}
          //       
          {...todo}
        />
      ))}
    </ul>
  );
};

export default TodoList;
